/* eslint-disable react/prefer-stateless-function */
import React, { useContext, useEffect } from 'react';
import uuidv4 from 'PlugAndPlay/_global/js/uuid';
import { watchModals } from '@react-aria/aria-modal-polyfill';
import { OverlayProvider } from '@react-aria/overlays';

import CreditContext from './data/CreditContext';
import Popover from '../../popover/jsx/popover';

import CreditBalance from './creditBalance';

/**
 * Credits Application
 */
export default function Credits({
    popover = false,
    summaryMode = false,
    balanceTypes,
}) {
    const { balances, getBalances, transactions, getTransactions } =
        useContext(CreditContext);

    // Initial data load
    useEffect(() => {
        // Get the balances for the types the app has been configured to display
        getBalances(balanceTypes.map((balanceType) => balanceType.type));
    }, [balanceTypes]);

    // Initial data load
    useEffect(() => {
        if (!summaryMode) {
            // Get the balances for the types the app has been configured to display
            // But filter down to only those types that have been configured to show the recent transactions
            getTransactions(
                balanceTypes
                    .filter((balanceType) => balanceType.showRecentTransactions)
                    .map((balanceType) => balanceType.type)
            );
        }
    }, [summaryMode, balanceTypes]);

    watchModals();

    return (
        <OverlayProvider className="no-wysiwyg credits-wrapper">
            {balanceTypes.map((balanceConfig) => {
                // If in popover mode, wrap it in a Popover object
                if (popover) {
                    const titleId = uuidv4(); // Popover needs a id for the dialog described by

                    return (
                        <Popover
                            key={balanceConfig.type}
                            titleId={titleId}
                            trigger={({ props, ref }) => {
                                return (
                                    <button
                                        ref={ref}
                                        {...props}
                                        type="button"
                                        className="credits-wrapper__action"
                                    >
                                        <svg className="svg-icon">
                                            <title>
                                                {`View ${balanceConfig.type} balance`}
                                            </title>
                                            <use
                                                href={`#${balanceConfig.icon}`}
                                            />
                                        </svg>
                                    </button>
                                );
                            }}
                            className="credits-popover"
                        >
                            <CreditBalance
                                {...balanceConfig}
                                balances={balances}
                                transactions={transactions}
                            />
                        </Popover>
                    );
                }

                // Else return just the content
                return (
                    <CreditBalance
                        key={balanceConfig.type}
                        {...balanceConfig}
                        balances={balances}
                        transactions={transactions}
                    />
                );
            })}
        </OverlayProvider>
    );
}
